.popup__input {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  margin: 0 auto 5px;
  width: 83.26%;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding-bottom: 8px;
  transition: all linear 0.1s;
}

.popup__input:focus {
  outline: none;
  border: 1px solid rgba(0, 0, 0, 1);
}