@import url(./__logo/header__logo.css);
@import url(./__link/header__link.css);
@import url(./__wrapper/header__wrapper.css);
@import url(./__info/header__info.css);
@import url(./__email/header__email.css);
@import url(./__exit/header__exit.css);

.header {
  max-width: 882px;
  margin: 45px auto 0;
}

.header::after {
  content: " ";
  display: block;
  height: 0;
  border: 1px solid #545454;
  opacity: 0.7;
  margin: 36px 0 0;
  width: 100%;
}

@media screen and (max-width: 619px) {
  .header {
    margin: 28px auto 0;
  }

  .header::after {
    margin: 28px 0 0;
  }
}
